<template>
  <div class="homeWrap">
    <!-- 头部导航栏 -->
    <header>
      <span class="logo">ele.me</span>
      <router-link to="/login">
        <span>{{userName?'':'登录|注册'}}</span>
        <router-link to="/profile"><i class="el-icon-user" v-if="isShow1"></i></router-link>
      </router-link>
    </header>
    <!-- 定位城市 -->
    <nav>
      <div>
        <span>当前定位城市：</span>
        <span class="r_span">定位不准确，请在城市列表中选择</span>
      </div>
      <router-link :to="{name:'c',params:{cityId:guessCity ? guessCity.id : 1}}">
        <span @click="setCid">{{guessCity ? guessCity.name : ''}}</span>
        <i class="el-icon-arrow-right"></i>
      </router-link>
    </nav>
    <!-- 热门城市 -->
    <section class="hot_city">
      <h4 class="city_title">热门城市</h4>
      <ul>
        <li v-for="c in hotCity" :key="c.id" class="city_style" @click="chooseCity(c.id)">{{c.name}}</li>
      </ul>
    </section>
    <!-- 所有城市 -->
    <section class="group_city">
      <ul>
        <li v-for="(city_arr,group_key,index) in groupCity" :key="group_key" class="g_li">
          <h4 class="city_title">{{group_key}}
            {{index?"":"(按字母排序)"}}
          </h4>
          <ul>
            <li v-for="city in city_arr" :key="city.id" class="city_style ellipsis" @click="chooseCity(city.id)">{{city.name}}</li>
          </ul>
        </li>
      </ul>
    </section>
  </div>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {
      guessCity:null,
      hotCity: [],
      groupCity: null,
      userName:'',
      isShow:false,
      isShow1:''
    };
  },
  created() {
    this.userName = localStorage.getItem("username")
    if (this.userName) {
      this.isShow1 = true
    }else{
      this.isShow1 = false
    }
    this.axios.get("/v1/cities", {
        params: { type: "guess" }
      }).then(res => {
        this.guessCity = res;
        // console.log(res)
      }).catch(err => {});
    this.axios.get("/v1/cities", {
        params: { type: "hot" }
      }).then(res => {
        this.hotCity = res;
      }).catch(err => {});
    this.axios.get("/v1/cities", {
        params: { type: "group" }
      }).then(res => {
        // this.groupCity = res;
        this.handleGroupCity(res);
      }).catch(err => {});
  },
  methods:{
    handleGroupCity(cityGroup){
      const orderCity = {};
      Object.keys(cityGroup).sort().forEach(key=>{
        orderCity[key] = cityGroup[key]
      });
      this.groupCity = orderCity;
    },
    chooseCity(cId){
      this.$router.push({name:'c',params:{cityId:cId}});
      localStorage.setItem("cityID",cId)
    },
    setCid(){
      localStorage.setItem("cityID",this.guessCity.id)
    }
  }
};
</script>

<style scoped>
header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  height: 1.95rem;
  background-color: #3190e8;
}
.logo {
  left: 0.4rem;
  font-weight: 400;
  font-size: 0.7rem;
  color: #fff;
  width: 2.3rem;
  height: 0.7rem;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
header > a {
  position: absolute;
  right: 0.55rem;
  color: #fff;
  font-size: 0.65rem;
  top: 50%;
  transform: translateY(-50%);
  text-decoration: none;
}
.el-icon-user{
  position: absolute;
  right: 0.4rem;
  color: #fff;
  font-size: 1rem;
  top: 50%;
  transform: translateY(-50%);
  text-decoration: none;
}
nav {
  width: 100%;
  padding-top: 2.35rem;
  background-color: #fff;
  margin-bottom: 0.4rem;
}
nav > div {
  display: flex;
  justify-content: space-between;
  padding: 0.45rem;
}
nav > div > span {
  font-size: 0.55rem;
  color: #666;
}
.r_span {
  font-weight: 900;
  font-size: 0.475rem;
  color: #9f9f9f;
}
nav > a {
  text-decoration: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 1.8rem;
  padding: 0 0.45rem;
  border-top: 1px solid #e4e4e4;
  border-bottom: 2px solid #e4e4e4;
  font: 0.75rem/1.8rem Microsoft YaHei;
}
nav > a > span {
  color: #3190e8;
}
.hot_city {
  background-color: #fff;
}
.city_title {
  color: #666;
  font-weight: 400;
  text-indent: 0.45rem;
  border-top: 2px solid #e4e4e4;
  border-bottom: 1px solid #e4e4e4;
  font: 0.55rem/1.45rem Helvetica Neue;
}
.hot_city > ul{
  overflow: hidden;
}
.city_style {
  float: left;
  text-align: center;
  color: #3190e8;
  border-bottom: .025rem solid #e4e4e4;
  border-right: .025rem solid #e4e4e4;
  width: 25%;
  height: 1.75rem;
  font-size: 0.6rem;
  line-height: 1.75rem;
  font-family: "Microsoft YaHei";
  box-sizing: border-box;
}
.g_li{
  margin-bottom: .4rem;
  background-color: #fff;
  border-bottom: 1px solid #e4e4e4;
}
.g_li ul {
  overflow: hidden;
}
</style>